import React, { useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';

const withLogger = (WrappedComponent) => {
    return (props) => {
        const ref = useRef();
        const { tasks } = useSelector((state) => state.list);

        useEffect(() => {
            if (ref.current) {
                if (ref.current.length && ref.current.length === tasks.length) {
                    ref.current.forEach((task, ind) => {
                        if (task.title !== tasks[ind].title) {
                            console.log(
                                `     ${task.title}  ${tasks[ind].title}`,
                            );
                        } else if (task.isCompleted !== tasks[ind].isCompleted) {
                            console.log(
                                `    ${task.title}  isCompleted: ${task.isCompleted}  ${tasks[ind].isCompleted}`,
                            );
                        }
                    });
                } else if (ref.current.length > tasks.length) {
                    const deletedTaskText = ref.current.find((task, ind) => {
                        return task.id !== tasks[ind]?.id;
                    }).title;
                    console.log(`   ${deletedTaskText}`);
                } else if (ref.current.length < tasks.length) {
                    console.log(
                        `    ${tasks[tasks.length - 1].title}`,
                    );
                }
            }
            ref.current = tasks;
        }, [tasks]);

        return <WrappedComponent {...props} />;
    };
};

export default withLogger;
